import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { Typography, Card, CardContent, CardMedia } from '@material-ui/core/';
import './Vacation.css'
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Favorite from '@material-ui/icons/Favorite';
import FavoriteBorder from '@material-ui/icons/FavoriteBorder';
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';
import EditOutlinedIcon from '@material-ui/icons/EditOutlined';
import { useDispatch, useSelector } from 'react-redux'

const Vacation = ({ vacation, vacations, setVactions }) => {

    const user = useSelector(state => state.user)
    const [status, setStatus] = useState(false)
    const dispatch = useDispatch()

    useEffect(() => {
        if (vacation.checked === true) {
            setStatus(true)
        } else if (vacation.checked === false) {
            setStatus(false)
        }
    }, [vacation.checked])

    const handleRemoveVacation = () => {
        dispatch({
            type: "REMOVE_VACATION",
            payload: vacation
        });
    }

    const handleEditVacation = () => {
        dispatch({
            type: "UPDATE_VACATION",
            payload: vacation
        });
    };

    const handleFollowVacation = async e => {
        try {
            if (e.target.checked) {
                const res = await fetch('http://localhost:1000/followvecation/add', {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ user_id: user.id, vacation_id: vacation.id })
                })
                const data = await res.json()
                console.log(data)
                setStatus(!status)

                const LikedVacation = vacations.filter(v => v.id === vacation.id)
                const unLikedVacations = vacations.filter(v => v.id !== vacation.id)
                const CombineVecations = [...LikedVacation, ...unLikedVacations]

                setVactions(CombineVecations)

            } else if (!e.target.checked) {
                const res = await fetch('http://localhost:1000/followvecation/remove', {
                    method: 'DELETE',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ user_id: user.id, vacation_id: vacation.id })
                })
                const data = await res.json()
                console.log(data)
                setStatus(!status)

                const unLikeVacation = vacations.filter(v => v.id === vacation.id)
                const LikedVacation = vacations.filter(v => v.id !== vacation.id)
                LikedVacation.push(...unLikeVacation)

                setVactions(LikedVacation)

            }

        } catch (err) {
            console.log(err)
        }
    };

    return (
        <Card className="Card">
            <CardMedia
                className="CardImage"
                image={vacation.image}
                title={vacation.name}
            />
            <CardContent className="CardContent">
                <Typography variant="h5" component="h2">
                    <div className="VacationName">
                        {vacation.country}
                    </div>
                </Typography>
                <Typography variant="body2" color="textSecondary">
                    <b>Check-in: </b>{vacation.godate},
                     <b>Check-out: </b>{vacation.backdate}
                </Typography>
                <Typography variant="body2" color="textSecondary">
                    <b>
                        {vacation.description}
                    </b>
                </Typography>
                <div style={{ display: "flex", justifyContent: "space-between" }}>

                    <Typography variant="body2" color="textSecondary">
                        <b>
                            {vacation.price}$
                        </b>
                    </Typography>
                    <div>
                        {user.role === 'admin' ?
                            <div>
                                <Link className="EditAndRemoveIcons" onClick={handleEditVacation} to="/editvacation"><EditOutlinedIcon /></Link>
                                <Link className="EditAndRemoveIcons" id={vacation.id} onClick={handleRemoveVacation} to="/removevacation"><DeleteOutlineIcon /></Link>
                            </div>

                            : <FormControlLabel onClick={handleFollowVacation} checked={status}
                                control={<Checkbox icon={<FavoriteBorder />} checkedIcon={<Favorite />} name="checkedH" />}
                            />
                        }
                    </div>
                </div>
            </CardContent>
        </Card>
    )
}

export default Vacation
