import React, { useState } from 'react'
import { Button } from '@material-ui/core/';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/Close';
import { Alert } from '@material-ui/lab/';
import { useSelector } from 'react-redux';
import './RemoveVacation.css';

const RemoveVacation = ({ history }) => {

    const vacation = useSelector(state => state.vacation)

    const [errMsg, setErrMsg] = useState('')
    const [SuccessMsg, setSuccessMsg] = useState('')

    const handelRemove = async () => {
        try {
            let res = await fetch(`http://localhost:1000/vacations/remove/${vacation.id}`, {
                method: 'DELETE'
            })
            let data = await res.json()
            if (data.err) {
                setErrMsg(data.msg)
                setTimeout(() => {
                    setErrMsg('')
                }, 1000)
                return
            }
            setSuccessMsg(data.msg)
            setTimeout(() => {
                setSuccessMsg('')
                history.push('/vacations')
            }, 1000)
        } catch (err) {
            console.log(err)
        }
    }

    const handleGoBack = () => {
        history.push('/vacations')
    }

    return (
        <div className="RemoveContainer">
            <div className="RemoveTEXT">
                <h1>Are you sure you want to remove {vacation.country} vacation ?</h1>
            </div>
            <div className="RemoveButtonContainer">
                <Button onClick={handleGoBack}><CloseIcon className="closeButton" /></Button>
                <Button onClick={handelRemove}><CheckIcon className="checkButton" /></Button>
            </div>
            { errMsg === '' ? "" : <Alert className="Alert" severity="error">{errMsg}</Alert>}
            { SuccessMsg === '' ? "" : <Alert className="Alert" severity="success">{SuccessMsg}</Alert>}
        </div >
    )
}

export default RemoveVacation
