import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { Grid } from '@material-ui/core/';
import Vacation from './Vacation/Vacation';
import './Vacations.css'
import AddVacation from './AddVacation/AddVacation';


const Vacations = ({ history }) => {
    const [vacations, setVactions] = useState([])

    const user = useSelector(state => state.user)

    useEffect(() => {
        (async () => {
            try {
                if (!user.firstname)
                    return history.push('/login')

                let res = await fetch(`http://localhost:1000/vacations/${user.id}`, {
                    method: 'GET',
                    headers: { 'token': localStorage.getItem('token') }
                })
                let data = await res.json()
                // console.log(data)
                setVactions(data)
            } catch (err) {
                console.log(err)
            }
        })()
    }, [history, user.firstname, user.id]);

    const handleAddVacation = () => {
        history.push('/addvacation')
    }

    return (
        <Grid
            className="CardsContainer"
            container
            spacing={2}
            direction="row"
            justify="center"
        >
            {user.role === 'admin' ?
                <AddVacation click={handleAddVacation} />
                : null
            }
            {vacations.map(vacation => <Vacation setVactions={setVactions} vacations={vacations} vacation={vacation} key={vacation.id} />)}
        </Grid>
    )
}

export default Vacations
