import React from 'react'
import './AddVacation.css'
import LibraryAddIcon from '@material-ui/icons/LibraryAdd';

const AddVacation = ({click}) => {
    return (
        <div className="AddVacationContainer" onClick={click}>
            <div className='Main'>
                <LibraryAddIcon className="ControlPointIcon" />
            </div>
        </div>
    )
}

export default AddVacation