const UserReducer = (state = {}, action) => {
    switch (action.type) {
        case 'LOGIN':
            return action.payload
        case 'LOGOUT':
            return {}
        default:
            return state
    }
}

const VacationsReducer = (state = {}, action) => {
    switch (action.type) {
        case 'ADD_VACATION':
            // console.log('ADD_VACATION:', action.payload)
            return {}
        case 'UPDATE_VACATION':
            // console.log('UPDATE_VACATION:', action.payload)
            return action.payload
        case 'REMOVE_VACATION':
            // console.log('REMOVE_VACATION:', action.payload)
            return action.payload
        default:
            return state
    }
}

export { UserReducer, VacationsReducer }

