import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { Paper, TextField, Button } from '@material-ui/core/';
import { Alert } from '@material-ui/lab/';
import './Register.css'

const Register = ({ history }) => {

    const [firstname, setFirstname] = useState('')
    const [lastname, setLastname] = useState('')
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [errMsg, setErrMsg] = useState('')
    const [SuccessMsg, setSuccessMsg] = useState('')


    const handleSubmit = async (e) => {
        try {
            e.preventDefault()
            const res = await fetch('http://localhost:1000/register/', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ firstname, lastname, username, password })
            })
            const data = await res.json()
            if (data.err) {
                setErrMsg(data.msg)
                setTimeout(() => {
                    setErrMsg('')
                }, 2000)
                return
            }
            setSuccessMsg(data.msg)
            setTimeout(() => {
                history.push('/login')
            }, 2000)
        } catch (err) {
            console.log(err)
        }
    }
    return (
        <div>
            <div className="RegisterTEXT">
                <h1>Register:</h1>
            </div>
            <div className="RegisterContainer">
                <Paper elevation={3} className="RegisterBox" >
                    <form className="RegisterForm" noValidate autoComplete="off">
                        <TextField onChange={e => setFirstname(e.target.value)} className="RegisterInput" id="standard-required" label="First name" variant="outlined" autoComplete='off' />
                        <TextField onChange={e => setLastname(e.target.value)} className="RegisterInput" id="standard-required" label="Last name" variant="outlined" autoComplete='off' />
                        <TextField onChange={e => setUsername(e.target.value)} className="RegisterInput" id="standard-required" label="User name" variant="outlined" autoComplete='off' />
                        <TextField onChange={e => setPassword(e.target.value)} className="RegisterInput" id="standard-required" label="Password" variant="outlined" autoComplete='off' />

                        <div className="RegisterButtonContainer">
                            <span>
                                already have account? <Link to="/login"> Login Now!</Link>
                            </span>
                            <Button onClick={handleSubmit} variant="contained" color="primary" type="submit" className="RegisterButton">
                                Register now
                            </Button>
                        </div>
                    </form>
                </Paper>
            </div>

            { errMsg === '' ? "" : <Alert className="Alert" severity="error">{errMsg}</Alert>}
            { SuccessMsg === '' ? "" : <Alert className="Alert" severity="success">{SuccessMsg}</Alert>}

        </div >
    )
}

export default Register
