import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Link, useLocation } from 'react-router-dom'
import { AppBar, Toolbar } from '@material-ui/core/';
import './Header.css'
import logo from './logo.png';

const Header = () => {
    const user = useSelector(state => state.user)

    const dispatch = useDispatch()
    const location = useLocation();

    const handleLogOut = () => {
        localStorage.removeItem('token')
        dispatch({
            type: 'LOGOUT'
        })
    }

    return (
        <div>
            <AppBar className="AppBar" position="static">
                <Toolbar>
                    <img src={logo} alt="logo" height='80px' width='400px'></img>
                    <div className="ButtonsContainer">
                        {user.role === 'admin' && location.pathname === '/vacations' ?
                            <Link className="HeaderButton" to='/reports'>Reports</Link>
                            : null
                        }

                        {(user.role === 'admin' && location.pathname === '/reports')
                            || location.pathname === '/addvacation'
                            || location.pathname === '/editvacation'
                            || location.pathname === '/removevacation' ?
                            <Link className="HeaderButton" to='/vacations'>Vacations</Link>
                            : null
                        }

                        {user.firstname === undefined ?
                            <Link onClick={handleLogOut} className="HeaderButton" to='/login'>Login</Link>
                            : <Link onClick={handleLogOut} className="HeaderButton" to='/login'>Logout</Link>
                        }
                    </div>
                </Toolbar>
            </AppBar>
        </div>
    )
}

export default Header
