import React, { useState, useEffect } from 'react'
import { useSelector } from 'react-redux'
import { TextField, Paper, Button, Typography, Card, CardMedia, CardContent } from '@material-ui/core/';
import { Alert } from '@material-ui/lab/';
import { useLocation } from 'react-router-dom'

import './EditAddVacation.css'

const EditAddVacation = ({ history }) => {

    const location = useLocation();

    const user = useSelector(state => state.user)
    const vacation = useSelector(state => state.vacation)
    
    const [country, setCountry] = useState('country')
    const [city, setCity] = useState('city')
    const [description, setDesctiption] = useState('description')
    const [godate, setGoDate] = useState('')
    const [backdate, setBackDate] = useState('')
    const [price, setPrice] = useState(0)
    const [image, setImage] = useState('https://icon-library.com/images/add-photo-icon/add-photo-icon-19.jpg')

    const [errMsg, setErrMsg] = useState('')
    const [SuccessMsg, setSuccessMsg] = useState('')


    useEffect(() => {
        if (user.role !== 'admin')
            return history.push('/vacations')

        if (location.pathname === '/editvacation') {
            setCountry(vacation.country)
            setCity(vacation.city)
            setDesctiption(vacation.description)
            setGoDate(vacation.godate)
            setBackDate(vacation.backdate)
            setPrice(vacation.price)
            setImage(vacation.image)
        }
    }, [history, user.role, location.pathname, vacation.country, vacation.city, vacation.description, vacation.godate, vacation.backdate, vacation.price, vacation.image])


    const handleEditVacation = async () => {
        try {
            const res = await fetch(`http://localhost:1000/vacations/edit/${vacation.id}`, {
                method: 'PUT',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ country, city, description, godate, backdate, price, image })
            })
            const data = await res.json()
            if (data.err) {
                setErrMsg(data.msg)
                setTimeout(() => {
                    setErrMsg('')
                }, 1000)
                return
            }
            setSuccessMsg(data.msg)
            setTimeout(() => {
                setSuccessMsg('')
                history.push('/vacations')
            }, 1000)
        } catch (err) {
            console.log(err)
        }
    }


    const handleAddVacation = async () => {
        try {
            const res = await fetch('http://localhost:1000/vacations/add', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ country, city, godate, backdate, image, description, price })
            })
            const data = await res.json()
            if (data.err) {
                setErrMsg(data.msg)
                setTimeout(() => {
                    setErrMsg('')
                }, 3000)
                return
            }
            setSuccessMsg(data.msg)
            setTimeout(() => {
                setSuccessMsg('')
            }, 3000)

            setCountry('Place Country')
            setCity('Place City')
            setDesctiption('Place Desctiption')
            setGoDate('')
            setBackDate('')
            setPrice(0)
            setImage('https://icon-library.com/images/add-photo-icon/add-photo-icon-19.jpg')

            console.log(data)
        } catch (err) {
            console.log(err)
        }
    }

    return (
        <>

            <div className="Addtitle">
                {location.pathname === '/editvacation' ?
                    <h2>Edit Vacation</h2>
                    : <h2>Add Vacation</h2>
                }
            </div>
            <div className="AddMainContainer">
                <div className="AddFromContainer">
                    <Paper elevation={3} className="AddBox" >
                        <form className="AddForm" noValidate autoComplete="off">
                            <TextField onChange={e => setCountry(e.target.value)} className="AddInput" value={country} type="text" placeholder="country" />
                            <TextField onChange={e => setCity(e.target.value)} className="AddInput" value={city} type="text" placeholder="city" />
                            <TextField onChange={e => setDesctiption(e.target.value)} className="AddInput" value={description} type="text" placeholder="desciption" />
                            <TextField onChange={e => setImage(e.target.value)} className="AddInput" value={image} type="text" placeholder="image" />
                            <TextField onChange={e => setGoDate(e.target.value)} className="AddInput" value={godate} type="date" placeholder="go date" />
                            <TextField onChange={e => setBackDate(e.target.value)} className="AddInput" value={backdate} type="date" placeholder="backdate" />
                            <TextField onChange={e => setPrice(e.target.value)} className="AddInput" value={price} type="number" placeholder="price" />
                            <div className="AddButtonContainer">

                                {location.pathname === '/editvacation' ?
                                    <Button onClick={handleEditVacation} variant="contained" color="primary" className="EditButton">
                                        Update now
                                    </Button>
                                    : <Button onClick={handleAddVacation} variant="contained" color="primary" className="AddButton">
                                        Add Vacation
                                    </Button>
                                }
                            </div>
                        </form>
                    </Paper>
                </div>
                <Card className="DisplayAddVacationCard">
                    <CardMedia
                        className="DisplayAddVacationCardImage"
                        image={image}
                        title={city}
                        alt="vacationimage"
                    />
                    <CardContent className="CardContent">
                        <Typography variant="h5" component="h2">
                            <div className="VacationName">
                                {country}
                            </div>
                        </Typography>
                        <Typography variant="body2" color="textSecondary">
                            <b>Check-in: </b>{godate},
                     <b>Check-out: </b>{backdate}
                        </Typography>
                        <div className="infoContainer">
                            <Typography variant="body2" color="textSecondary">
                                <b>
                                    {description}
                                </b>
                            </Typography>
                            <Typography variant="body2" color="textSecondary">
                                <b>{price}$</b>
                            </Typography>
                        </div>
                    </CardContent>
                </Card>
            </div>
            <div className="AlertContainer">
                {errMsg === '' ? "" : <Alert className="Alert" severity="error">{errMsg}</Alert>}
                {SuccessMsg === '' ? "" : <Alert className="Alert" severity="success">{SuccessMsg}</Alert>}
            </div>
        </ >
    )
}

export default EditAddVacation
