import React, { useEffect } from 'react'
import { useState } from 'react';
import { Bar } from 'react-chartjs-2';


const AdminReports = () => {

    const [followedVacations, setFollowedVacations] = useState([])

    useEffect(() => {
        (async () => {
            const res = await fetch('http://localhost:1000/followvecation/reports')
            const data = await res.json()
            // console.log(data)
            setFollowedVacations(data)
        })()
    }, [])

    const data = {
        labels: ["", ...followedVacations.map(fv => fv.city)],
        datasets: [
            {
                label: 'Followed Vacations',
                fill: false,
                lineTension: 0.1,
                backgroundColor: 'rgba(75,192,192,0.4)',
                borderColor: 'rgba(75,192,192,1)',
                borderCapStyle: 'butt',
                borderDash: [],
                borderDashOffset: 0.0,
                borderJoinStyle: 'miter',
                pointBorderColor: 'rgba(75,192,192,1)',
                pointBackgroundColor: '#fff',
                pointBorderWidth: 1,
                pointHoverRadius: 5,
                pointHoverBackgroundColor: 'rgba(75,192,192,1)',
                pointHoverBorderColor: 'rgba(220,220,220,1)',
                pointHoverBorderWidth: 2,
                pointRadius: 1,
                pointHitRadius: 10,
                yAxisID:0,
                data: [0, ...followedVacations.map(fv => fv.usersFollow)]
            }
        ]
    };
    return (
        <div>
            {!followedVacations.length ? "no data" : <Bar data={data} height={100} />}

            {console.log(followedVacations)}
        </div>
    )
}

export default AdminReports
