import React, { useEffect } from 'react'
import { BrowserRouter as Router, Route } from 'react-router-dom'
import Header from './Components/Header/Header'
import Login from './Components/Login/Login'
import Register from './Components/Register/Register'
import Vacations from './Components/Vacations/Vacations'
import jwt_decode from 'jwt-decode'
import { useDispatch } from 'react-redux'
import RemoveVacation from './Components/Vacations/RemoveVacation/RemoveVacation'
import AdminReports from './Components/AdminPanel/Reports/AdminReports'
import EditAddVacation from './Components/AdminPanel/EditAddVacation/EditAddVacation'


const App = () => {
  const dispatch = useDispatch()

  useEffect(() => {
    (async () => {
      if (!localStorage.token)
        return;

      const decoded = await jwt_decode(localStorage.token)

      dispatch({
        type: 'LOGIN',
        payload: decoded
      })
    })()
  }, [dispatch])

  return (
    <div>
      <Router>
        <Header />
        <Route path='/login' component={Login} />
        <Route path='/register' component={Register} />
        <Route path='/vacations' component={Vacations} />
        <Route path='/addvacation' component={EditAddVacation} />
        <Route path='/removevacation' component={RemoveVacation} />
        <Route path='/editvacation' component={EditAddVacation} />
        <Route path='/reports' component={AdminReports} />
        <Route path='/' exact component={Vacations} />
      </Router>
    </div>
  )
}

export default App
