import React, { useEffect, useState } from 'react'
import jwt_decode from 'jwt-decode'
import { useDispatch, useSelector } from 'react-redux'
import { Paper, TextField, Button } from '@material-ui/core/';
import { Alert } from '@material-ui/lab/';

import { Link } from 'react-router-dom'
import './Login.css'


const Login = ({ history }) => {

    const dispatch = useDispatch()
    const user = useSelector((state) => state.user)

    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [errMsg, setErrMsg] = useState('')

    useEffect(() => {
        if (user.firstname)
            history.push('/vacations')
    }, [history, user])


    const handleClick = async (e) => {
        try {
            e.preventDefault()
            let res = await fetch('http://localhost:1000/login/', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ username, password })
            });
            let data = await res.json()
            if (data.err) {
                setErrMsg(data.msg)
                setTimeout(() => {
                    setErrMsg('')
                }, 2000)
                return
            }
            const decoded = jwt_decode(data.token)
            localStorage.token = data.token

            dispatch({
                type: 'LOGIN',
                payload: decoded
            });
            history.push('/vacations')

        } catch (err) {
            console.log(err)
        }
    };

    return (
        <div>
            <div className="LoginTEXT">
                <h1>Welcome</h1>
            </div>
            <div className="LoginContainer">
                <Paper elevation={3} className="LoginBox" >
                    <form className="LoginForm" noValidate autoComplete="off">
                        <TextField onChange={e => setUsername(e.target.value)} className="LoginInput" id="standard-required" label="Name" variant="outlined" autoComplete='off' />
                        <TextField onChange={e => setPassword(e.target.value)} className="LoginInput" id="outlined-basic" type="password" variant="outlined" autoComplete='off' />
                        <div className="LoginButtonContainer">
                            <span>
                                Don't have an account? <Link to="/register"> Register Now!</Link>
                            </span>
                            <Button onClick={handleClick} variant="contained" color="primary" type="submit" className="Button">
                                LOG IN
                            </Button>
                        </div>
                    </form>
                    {errMsg === '' ? "" : <Alert className="Alert" severity="error">{errMsg}</Alert>}
                </Paper>

            </div>


            {/* <form>
                <input onChange={e => setUsername(e.target.value)} type="text" />
                <input onChange={e => setPassword(e.target.value)} type="password" autoComplete="off" />
                <button onClick={handleClick}>Login</button>
            </form> */}
        </div>


    );
};

export default Login
